
import React from "react";
import { connect } from "react-redux";
import { systemAuthBaseUrl } from "../../../config/config";
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import { getErrorMessage, isEmpty } from "../../../utils/utils";
import Loader from "../../components/loader/loader";
import Notifier from "../../components/notifier/notifier";
import RawTitleValue from "../../components/title_value/title_value";



class BankTranserPayment extends React.Component {

    state = {
        hasAgreed: false,
        bankInformation: {},
        referenceNumber: '',
        isAsyncCall: false,
        errorMessage: '',
    }
    render() {
        return <div className="steps_holder">
            <p>{{
                fr: "Comment payer par transfre bancaire ?",
                en: "How to pay by bank tranfer ?",
                sw: "Jinsi ya kulipa kwa uhamisho wa benki?"
            }[`${this.props.currentLanguage}`]}</p>
            {!isEmpty(this.state.errorMessage) ? <Notifier type='error' message={this.state.errorMessage} /> : <></>}
            <div className="steps_holder_b_transfer">

                <p>
                    {{
                        fr: `Pour activer votre abonnement, veuillez transfrer la somme de ${this.props.SubscriptionType.Price}$
                         notre agent de paiement 
                        ${this.state.hasAgreed ? ' numrs ci-dessous' : ''} en citant comme nom de l'expditeur le numro de rfrence 
                         ${this.state.hasAgreed ? `${this.state.referenceNumber}` : ' qui sera gnr'}.
                        Le numro de rfrence est valable 24 heures, date  laquelle le paiement est attendu. Si le numro de rfrence n'est pas inclus dans les notes avec le transfert
                        , nous ne pourrons pas activer votre abonnement, cela inlcut l'irrversibilit de l'opration, d'o la perte de votre argent.`,
                        
                        // ${<span className={`${this.state.hasAgreed && 'refCodeP'}`}>

                        en: `To activate your subscription, please transfer the sum of ${this.props.SubscriptionType.Price}$
                        to our payment agent ${this.state.hasAgreed ? ' listed below': ''}, quoting as the name of the sender the reference number
                        ${this.state.hasAgreed ? `${this.state.referenceNumber}` : ' which will be generated'}
                        The reference number is valid for 24 hours, at which time payment is expected. If the reference
                        number is not included in the notes with the transfer, we will not be able to activate your
                        subscription. This includes the irreversibility of the operation, hence the loss of your money. `,

                        sw : `Ili kuwezesha usajili wako, tafadhali hamisha jumla ya ${this.props.SubscriptionType.Price}$ 
                        kwa wakala wetu wa malipo ${this.state.hasAgreed ? ' iliyoorodheshwa hapa chini' : ''}, kunukuu kama jina la mtumaji nambari ya kumbukumbu
                        ${this.state.hasAgreed ? `${this.state.referenceNumber}` : ' ambayo itazilishwa. '}
                        Nambari ya kumbukumbu ni halali kwa saa 24, wakati ambao malipo yanatarajiwa. Ikiwa nambari ya marejeleo haijajumuishwa kwenye madokezo na uhamishaji,
                        hatutaweza kuwezesha usajili wako. Hii ni pamoja na kutoweza kutenduliwa kwa operesheni, hivyo kupoteza pesa zako.
                       `
                    }[`${this.props.currentLanguage}`]}
                </p>

                <div className="divider" />
                {!this.state.hasAgreed
                    ? <> {this.state.isAsyncCall
                        ? <div className="full_space_loader"><Loader /></div>
                        : <div className="accept_bank_transfer_conditions" onClick={(event) => {
                            event.stopPropagation();
                            this.onRequestingPaimentIntent();
                        }}>
                            <p>{{
                                fr : "J'ai lu et j'accepte de payer par virement bancaire",
                                en : "I have read and agreed to pay by bank tranfer",
                                sw : "Nimesoma na kukubali kulipa kwa uhamisho wa benki"
                            }[`${this.props.currentLanguage}`]}</p>
                            <i className="fa-solid fa-arrow-right-long" />
                        </div>
                    }
                    </>
                    : <>
                        <RawTitleValue title={{
                            fr : "Votre numro de rfrence",
                            en : "Your reference number",
                            sw : "Nambari yako ya kumbukumbu"
                        }[`${this.props.currentLanguage}`]} value={`${this.state.referenceNumber}`} />
                        <RawTitleValue title={{
                            fr : "Nom de l'Agent",
                            en : "Agent Name",
                            sw : "Jina la wakala"
                        }[`${this.props.currentLanguage}`]} value={`${this.state.bankInformation.BankName}`} />
                        <RawTitleValue title={{
                            fr : "Ville",
                            en : "City",
                            sw : "Mji"
                        }[`${this.props.currentLanguage}`]} value={`${this.state.bankInformation.City}`} />
                        <RawTitleValue title='SWIFT Code' value={`${this.state.bankInformation.SwiftCode}`} />
                        <RawTitleValue title='IBAN' value={`${this.state.bankInformation.Iban}`} />
                        <RawTitleValue title={{
                            fr : "Bnficiaire",
                            en : "Payee",
                            sw : "Mlipwaji"
                        }[`${this.props.currentLanguage}`]} value={`${this.state.bankInformation.Payee}`} />
                        <RawTitleValue title={{
                            fr : "Numro du compte bancaire",
                            en : "Bank account number",
                            sw : "Nambari ya akaunti ya benki"

                        }[`${this.props.currentLanguage}`]} value={`${this.state.bankInformation.BankAccountNumber}`} />
                        <RawTitleValue title={{
                            fr : "Pays",
                            en : "Country",
                            sw : "Inchi"
                        }[`${this.props.currentLanguage}`]} value={`${this.state.bankInformation.Country}`} />
                    </>}
            </div>
        </div>
    }

    onRequestingPaimentIntent() {
        this.setState({ isAsyncCall: true, errorMessage: '' });
        this.props.isAsync(true);
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: 'api/payment/bank-transfer-create-intent',
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: this.props?.userInformation?.accessToken,
                },
                body: {
                    SubscriptionTypeId: this.props.SubscriptionType.id,
                }
            },
        }).then((response) => {
            if (typeof (response.error) === 'undefined') {
                console.log(response.response);
                const { CodeRef, BankName, Country, City, SwiftCode, Payee, BankAccountNumber, Iban } = response.response;
                this.props.isAsync(false);
                this.setState({
                    isAsyncCall: false, hasAgreed: true, referenceNumber: CodeRef, bankInformation: {
                        BankName: BankName,
                        Country: Country,
                        City: City,
                        SwiftCode: SwiftCode,
                        Payee: Payee,
                        BankAccountNumber: BankAccountNumber,
                        Iban: Iban,
                    }
                });
            } else {
                this.props.isAsync(false);
                this.setState({ isAsyncCall: false, errorMessage: getErrorMessage('e13', 'en') });
            }
        });
    }
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage.code,
    userInformation: storeOf.madoshome.userInformation,
    subscriptionPlans: storeOf.madoshome.subscriptionPlans,
});

export default connect(mapStateToProps)(BankTranserPayment);