import './history.css'
import React, { createRef } from 'react';
import { connect } from 'react-redux';
// import historyImg from '../../../assets/svgs/historyNotFound.svg'
import CallToSubscription from '../call_to_subscription/call_to_subscription';
import ApiProvider, { ApiMethods } from '../../../data/providers/api_services';
import { appName, systemAuthBaseUrl } from '../../../config/config';
import { useSelector } from 'react-redux';
import ListViewBuilder from '../../components/list_view_builder/listViewBuilder';
import { CardObserver } from '../../components/cards_displayer/cards_displayer';
import Loader from '../../components/loader/loader';
import notFoundimg from '../../../assets/svgs/noSbPayment.svg'
import NotFound from '../../components/entity_displayer/itemsNotFound';

class SubscriptionHistory extends React.Component {
    state = {
        isDetailsDisplayed: false,
        subscriptionHistory: [],
        isAsyncCall: false,
        isAsyncMore: false,
    }


    componentDidMount() {
        if ([...this.props.items].length) {
            this.setState({ isAsyncCall: false, subscriptionHistory: this.props.items });
            // console.log(this.state.subscriptionHistory)
        } else {
            this.loading();
        }
    }

    displayerRef = createRef();

    max = 50;

    loading() {
        if (!this.state.isAsyncMore) {
            this.setState({ isAsyncMore: true });
            this.onFetchSubscription(this.state.subscriptionHistory.length ? this.state.subscriptionHistory[this.state.subscriptionHistory.length - 1].id : 0).then(async (subscriptionHistory) => {
                await this.setState({ isAsyncMore: false, isAsyncCall: false, });
                if (subscriptionHistory.length) {
                    if (this.state.subscriptionHistory.length < this.max) {
                        const subscriptionHistoryList = [...this.state.subscriptionHistory, ...subscriptionHistory];
                        await this.setState({ subscriptionHistory: subscriptionHistoryList });
                    } else {
                        await this.setState({ subscriptionHistory: subscriptionHistory });
                    }
                } else {
                    await this.setState({ maxReached: true });
                }
            });
        }
    }


    render() {

        return (
            <div className="subscription_history_container">
                {!this.state.isAsyncCall && <NotFound
                    Data={this.state.subscriptionHistory}
                    Message = {{
                        fr: "Vous n'avez pas d'historique d'abonnement, vous tes sur le forfait gratuit.",
                        en: "You have no subscription history, you are on the free plan.",
                        sw: "Huna historia ya usajili, uko kwenye mpango usiolipishwa."
                    }}
                    onRetry = {(event)=>{
                        event.stopPropagation();
                        this.setState({isAsyncCall : true })
                        this.loading()
                        // console.log("retrying ...")
                    }}
                    imgToShow={notFoundimg}
                />}
                {this.state.isDetailsDisplayed ?
                    <CallToSubscription onClosePressed={() => { this.setState({ isDetailsDisplayed: !this.state.isDetailsDisplayed }) }} />
                    : <></>}
                <div className="main_history_container">
                    {
                        <div className="card_history_container">
                            <div className='action_button card_history_container_button flex' onClick={(event) => {
                                event.stopPropagation();
                                this.setState({ isDetailsDisplayed: !this.state.isDetailsDisplayed });
                            }}>
                                <i className='fa fa-crown' />
                                <h6>{{
                                    fr : "Choisir une plan d'abonnement",
                                    en : "Choose a suscription plan",
                                    sw : "Chagua mpango wa usajili"
                                    
                                }[`${this.props.currentLanguage}`]}</h6>
                            </div>
                            <ListViewBuilder
                                itemCount={this.state.isAsyncMore ? this.state.subscriptionHistory.length + 1 : this.state.subscriptionHistory.length}
                                itemBuilder={(index) => {
                                    const singlehistory = this.state.subscriptionHistory[index];
                                    const isTheTrueLastOne = (this.state.subscriptionHistory.length - 1) === index;
                                    return Object.keys(singlehistory ?? {}).length > 0
                                        ? isTheTrueLastOne
                                            ? <CardObserver
                                                displayerRef={this.displayerRef}
                                                onReachBottom={() => {
                                                    if (!this.state.maxReached) {
                                                        this.loading();
                                                    }
                                                }}
                                                key={index}><Cardhistory
                                                    key={singlehistory.id}
                                                    subscriptionId={singlehistory.SubscriptionTypeId}
                                                    status={!singlehistory.Actif}
                                                    expiredAt={singlehistory.To}
                                                    suscribeAt={singlehistory.createdAt}
                                                    currentLanguage= {this.props.currentLanguage}
                                                /></CardObserver>
                                            : <Cardhistory
                                                key={singlehistory.id}
                                                subscriptionId={singlehistory.SubscriptionTypeId}
                                                status={!singlehistory.Actif}
                                                expiredAt={singlehistory.To}
                                                suscribeAt={singlehistory.createdAt}
                                                currentLanguage= {this.props.currentLanguage}

                                            />
                                        : this.state.isAsyncMore ? <div className="full_space_loader"><Loader /></div> : <div />
                                }}
                                onReachBottom={() => { }}
                            />
                        </div>
                        // : <div className='unavailable_history'>
                        //     <img src={historyImg} alt="historyerro" width={350} height={350} />
                        //     <p>No subscription history was found with your account <i onClick={() => { this.setState({ isDetailsDisplayed: true }) }} >Subscribe !</i></p>
                        // </div>
                    }
                </div>
            </div>
        )
    }
    async onFetchSubscription(lastId) {
        this.setState({ isAsyncCall: !this.state.isAsyncCall })
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: `api/history-sp?offset=${lastId}`,
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: localStorage.getItem(`${appName}act`) ?? ''
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const subscriptions = response.response;
                // console.log(response.response)
                this.props.onItems(subscriptions);

                return subscriptions;
            } else {
                this.props.onItems([]);
                return [];
            }
        });
    };
}
export function Cardhistory({ subscriptionId, status, expiredAt, suscribeAt, currentLanguage }) {

    const { SubscriptionTypeList } = useSelector((storeOf) => ({
        SubscriptionTypeList: storeOf.madoshome.subscriptionPlans,
    }))

    return (
    
        <div className="single_card_history">
            <div className="card_history_header">
                <h5>
                    {SubscriptionTypeList.filter((e) => e.id === subscriptionId)[0]?.Name + " "}
                    - {SubscriptionTypeList.filter((e) => e.id === subscriptionId)[0]?.Price} USD
                </h5>
                <span id='cross_container'>
                    <i className="fa-solid fa-crown"></i>
                </span>
            </div>
            <div className="card_details">
                <div className="single_detail_content">
                    <p>{{
                        fr : "Statut",
                        en : "Status",
                        sw : "Hali"
                    }[`${currentLanguage}`]} </p>
                    {/* 'state expired out_of_bound' */}
                    <span className={status ? "expired" : "state"}>
                        <p id={status ? 'expired' : ''}>{status ? "Expired" : "Active"}</p>
                    </span>
                </div>

                <div className="single_detail_content">
                    <p>{{
                        fr : "Abonn le",
                        en : "Subscribe on",
                        sw : "Umejiandikisha kwenye"
                    }[`${currentLanguage}`]}</p>
                    <h4>{new Date(suscribeAt).toLocaleDateString()}</h4>
                </div>
                <div className="single_detail_content">
                    <p>{{
                        fr : "Date d'expiration",
                        en : "Expiry date",
                        sw : "Tarehe ya mwisho wa usajili wako"
                    }[`${currentLanguage}`]}</p>
                    <h4>{new Date(expiredAt).toLocaleDateString()}</h4>
                </div>
                <div className="single_detail_content">
                    <p>{{
                        fr : "Publications",
                        en : "Posts",
                        sw : "Machapisho"
                    }[`${currentLanguage}`]}  </p>
                    <h4>
                        {SubscriptionTypeList.filter((e) => e.id === subscriptionId)[0]?.Post}
                    </h4>
                </div>
            </div>
        </div>

    )

}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage.code,
    userInformation: storeOf.madoshome.userInformation,
    SubscriptionTypeList: storeOf.madoshome.SubscriptionTypeList,
});

export default connect(mapStateToProps)(SubscriptionHistory)