import React from "react";
import { connect } from "react-redux";
import BankTranserPayment from "./bank_transfer";
import './call_to_subscription.css';
import StripePayment from "./stripe_payment";



class CallToSubscription extends React.Component {

    state = {
        selectedPlan: -1,
        selectedMethod: -1,
        currentStep: 0,
        hasReachedTheEnd: false,
        isAsyncCall: false,
        selectedMethodCheckout: <div />,
    }

    sbPlans = [
        {
            'name': 'Sliver',
            'price': 10,
            'duration': 3,
            'posts': 10,
        },
        {
            'name': 'Gold',
            'price': 15,
            'duration': 6,
            'posts': 30,
        },
        {
            'name': 'Diamond',
            'price': 24,
            'duration': 12,
            'posts': 100,
        },
    ]

    paymentMethdos = [
        {
            'name': 'Credit/Debit Card',
            'isRec': true,
            'onClick': () => {
                this.setState({ selectedMethodCheckout: <StripePayment SubscriptionTypeId={this.props.subscriptionPlans[this.state.selectedPlan < 0 ? 0 : this.state.selectedPlan].id} isAsync={(isAsync) => { this.setState({ isAsyncCall: isAsync }) }} /> })
            },
        },
        {
            'name': 'Bank Transfer',
            'isRec': false,
            'onClick': () => {
                this.setState({ selectedMethodCheckout: <BankTranserPayment SubscriptionType={this.props.subscriptionPlans[this.state.selectedPlan < 0 ? 0 : this.state.selectedPlan]} isAsync={(isAsync) => { this.setState({ isAsyncCall: isAsync }) }} /> })
            },
        },
    ]


    render() {
        const waitingmsg = {
            'fr': "Patientez s'il vous plait...",
            'en': "Please wait...",
            'sw': "Tafadhali subiri..."
        }[`${this.props.currentLanguage}`]

        // const publish = {
        //     'fr': "Publier ",
        //     'en': "Publish ",
        //     'sw': "Kuchapisha"
        // }[`${this.props.currentLanguage}`]
        const close = {
            'fr': "Fermer ",
            'en': "Close ",
            'sw': "Funga"
        }[`${this.props.currentLanguage}`]
        const next = {
            'fr': "Suivant ",
            'en': "Next ",
            'sw': "ijayo"
        }[`${this.props.currentLanguage}`]
        const steps = [this.subscriptionPlansDisplayer(), this.paimentMethodsDisplayer(), this.paimentCheckoutDisplayer()];
        return <div className='modal_rideau'>
            <div className="dif_modal call_to_subcsription_left_lol">
                <div className="call_to_subcsription_left">
                    <div className="flex call_to_subcsription_header">
                        <div>
                            <h2>{{
                                fr: "Etre diteur",
                                en: "Be a publisher",
                                sw: "Kuwa mchapishaji"
                            }[`${this.props.currentLanguage}`]}</h2>
                            <p>
                                {{
                                    fr: "Slectionnez l'offre qui correspond le mieux  vos besoins.",
                                    en: "Choose the version that best suits your needs.",
                                    sw: "Chagua toleo linalofaa zaidi mahitaji yako."
                                }[`${this.props.currentLanguage}`]}
                            </p>
                        </div>
                        <span id='cross_container' onClick={() => { this.props.onClosePressed() }}>
                            <i className="fa-solid fa-xmark"></i>
                        </span>
                    </div>
                    <div className="call_to_subcsription_steps_indicator flex">
                        {[...steps].map((e, i) => {
                            const currS = i === this.state.currentStep;
                            return <span key={i} id={currS ? "call_to_subcsription_steps_indicator_selected" : ''} />
                        })}
                    </div>
                    <div className="call_to_subcsription_steps">
                        {steps[this.state.currentStep]}
                    </div>
                    {/* <div className="divider" /> */}
                    <div className="call_to_subcsription_buttons flex">
                        {this.state.currentStep !== 0 && <div className='action_button flex' onClick={(event) => {
                            event.stopPropagation();
                            this.backTo(this.state.currentStep);
                        }}>
                            <i className='fa fa-arrow-left' />
                            <h6>{{
                                fr: "Retour",
                                en: "Back",
                                sw: "Rudi"
                            }[`${this.props.currentLanguage}`]}</h6>
                        </div>}
                        <div className='action_button flex' onClick={(event) => {
                            event.stopPropagation();
                            if (!this.state.isAsyncCall) {
                                if (this.state.hasReachedTheEnd) {
                                    this.props.onClosePressed();
                                } else {
                                    this.jumpTo(this.state.currentStep, steps.length);
                                }
                            }
                        }}>
                            {!this.state.hasReachedTheEnd && <i className='fa fa-arrow-right' />}
                            <h6>{this.state.hasReachedTheEnd ? this.state.isAsyncCall ? waitingmsg : close : next }</h6>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }

    subscriptionPlansDisplayer() {
        return <div className="steps_holder">
            {[...this.props.subscriptionPlans].map((e, i) => {
                let isS = this.state.selectedPlan === i;
                return <div key={e.id} className={`sbPlan_card ${isS && 'sbPlan_card_selected'} flex`} onClick={(event) => {
                    event.stopPropagation();
                    this.setState({ selectedPlan: i });
                }}>
                    <span />
                    <div className="sbPlan_card_tile">
                        <h5>{e.Name}</h5>
                        <p>{e.Post} {{
                            fr : "publications",
                            en : "posts",
                            sw : "machapisho"
                        }[`${this.props.currentLanguage}`]}</p>
                    </div>
                    <p className="sbPlan_card_tile_price">${e.Price}/{e.Duration}{{
                        fr : "m",
                        en : "mo",
                        sw : "m"
                    }[`${this.props.currentLanguage}`]}</p>
                </div>
            })}
        </div>
    }

    paimentMethodsDisplayer() {
        return <div className="steps_holder">
            {/* <p>Select a payment method that best suits your needs</p> */}
            {[...this.paymentMethdos].map((e, i) => {
                let isS = this.state.selectedMethod === i;
                return <div key={i} className={`sbPlan_card ${isS && 'sbPlan_card_selected'} flex`} onClick={(event) => {
                    event.stopPropagation();
                    this.setState({ selectedMethod: i });
                    e.onClick();
                }}>
                    <span />
                    <div className="sbPlan_card_tile">
                        <h5>{e.name}</h5>
                        {e.isRec && <div ><h6 className="isRecommended">{{
                            fr : "Recommande",
                            en : "Recommanded",
                            sw : "Ilipendekeza"
                        }[`${this.props.currentLanguage}`]}</h6></div>}
                    </div>
                </div>
            })}
        </div>
    }

    paimentCheckoutDisplayer() {
        return <div>
            {this.state.selectedPlan >= 0 && <>{this.state.selectedMethodCheckout}</>}
        </div>
    }

    jumpTo(index, maxL) {
        if (this.state.selectedPlan < 0) {
            return;
        } else {
            if ((index + 1) === maxL) {
                this.setState({ hasReachedTheEnd: true });
                return;
            } else {
                if (this.state.currentStep > 0) {
                    if (this.state.selectedMethod < 0) {
                        return;
                    }
                }
                let ind = index + 1;
                if ((ind + 1) === maxL) {
                    this.setState({ hasReachedTheEnd: true });
                }
                this.setState({ currentStep: index + 1 });
            }
        }
    }

    backTo(index) {
        if (index === 0) {
            return;
        }
        this.setState({ currentStep: index - 1, hasReachedTheEnd: false, selectedMethodCheckout: <div />, selectedMethod: -1 });
    }
}


const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage.code,
    subscriptionPlans: storeOf.madoshome.subscriptionPlans,
});

export default connect(mapStateToProps)(CallToSubscription);