import React from "react";
import { connect } from "react-redux";
import {systemAuthBaseUrl } from "../../../config/config"; 
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import { getErrorMessage, isEmpty } from "../../../utils/utils";
import Loader from "../../components/loader/loader";
import Notifier from "../../components/notifier/notifier";
import CheckoutForm from "../../components/stripe_chekout/checkout";


class StripePayment extends React.Component {


    state = {
        clientSecret: '',
        isAsyncCall: false,
        hasLoaded: false,
        errorMessage: '',
    }

    componentDidMount() {
        this.onRequestingPaimentIntent();
    }


    render() {
        return <>
            {!isEmpty(this.state.errorMessage) && <Notifier type='error' message={this.state.errorMessage} />}
            {this.state.isAsyncCall
                ? <div className="full_space_loader"><Loader /></div>
                : this.state.hasLoaded
                && <div className="steps_holder">
                    <p>
                        {{
                            fr : "Il vous reste une tape pour devenir un diteur madoshome.",
                            en : "You are one step left to become a madoshome publisher",
                            sw : "Umesalia hatua moja kuwa mchapishaji wa madoshome."
                        }[`${this.props.currentLanguage}`]}
                    </p>
                    <CheckoutForm
                        clientSecret={this.state.clientSecret}
                        isAsync={this.props.isAsync}
                    />
                </div>

            }
        </>
    }


    onRequestingPaimentIntent() {
        this.setState({ isAsyncCall: true, errorMessage: '' });
        this.props.isAsync(true);
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: 'api/payment/tsp-owner-stripe',
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: this.props?.userInformation?.accessToken,
                },
                body: {
                    SubscriptionTypeId: this.props.SubscriptionTypeId,
                }
            },
        }).then((response) => {
            if (typeof (response.error) === 'undefined') {
                this.props.isAsync(false);
                this.setState({ isAsyncCall: false, hasLoaded: true, clientSecret: response.response.client_secret });
            } else {
                this.props.isAsync(false);
                this.setState({ isAsyncCall: false, errorMessage: getErrorMessage(response.error, 'en') });
            }
        });
    }
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage.code,
    userInformation: storeOf.madoshome.userInformation,
    subscriptionPlans: storeOf.madoshome.subscriptionPlans,
});

export default connect(mapStateToProps)(StripePayment);