import React from 'react';
import { useStripe, useElements, CardElement } from '@stripe/react-stripe-js';
import { useSelector } from 'react-redux';
import CardSection from './card_section';
import Notifier from '../notifier/notifier';
import { appName } from '../../../config/config';
import { isEmpty } from '../../../utils/utils';


export default function CheckoutForm({ clientSecret = '', isAsync = () => { } }) {
    const { userInformation, currentLanguage } = useSelector((storeOf) => ({
        userInformation: storeOf.madoshome.userInformation,
        currentLanguage: storeOf.madoshome.currentLanguage,
    }));
    const stripe = useStripe();
    const elements = useElements();
    const [errorMessage, setErrorMessage] = React.useState('');
    const [successMessage, setSuccessMessage] = React.useState('');
    const [isAsyncCall, setAsync] = React.useState(false);
    const formRef = React.useRef();
    const handleSubmit = async (event) => {
        event.preventDefault();
        setAsync(true);
        isAsync(true);
        if (!stripe || !elements) {
            return;
        }
        const result = await stripe.confirmCardPayment(clientSecret, {
            payment_method: {
                card: elements.getElement(CardElement),
                billing_details: {
                    name: userInformation.Fullname,
                    email: userInformation.Email,
                },
            }
        });

        if (result.error) {
            setAsync(false);
            isAsync(false);
            setErrorMessage(result.error.message)
        } else {
            if (result.paymentIntent.status === 'succeeded') {
                setAsync(false);
                isAsync(false);
                formRef.current.reset();
                setSuccessMessage(`${inter[currentLanguage.code]}`)
            }
        }
    };

    const inter ={
        fr: `${userInformation.Fullname}, c'est ${appName} et nous sommes heureux de vous informer que votre paiement pour le plan d'abonnement a t effectu avec succs!
        Afin de voir les mises  jour, rechargez la page si vous tes sur le Web ou redmarrez l'application si vous tes sur mobile pour rafrachir.
        Pour plus d'informations : https://madoshome.com/terms-and-conditions/`,
        en: `${userInformation.Fullname} this is ${appName} and we are pleased to inform you that your payment for the subscription plan has been successfully completed!
        In order to see the updates, reload the page if you are on the web or restart the app if you are on mobile.
        For more information: https://madoshome.com/terms-and-conditions//`,
        sw: `${userInformation.Fullname} ni ${appName} na sisi tuna furaha kukufahamisha kwamba malipo yako ya mpango wa usajili yamekamilika! Kwa habari zaidi: https://madoshome.com/terms-and-conditions/`,
    }
    return (
        <form ref={formRef} onSubmit={handleSubmit} className='checkout_form_stripe'>
            {!isEmpty(errorMessage) && <Notifier type='error' message={errorMessage} />}
            {!isEmpty(successMessage) && <Notifier type='success' message={successMessage} />}
            {isEmpty(successMessage) && <div>
                <CardSection />
                <button className={`${isAsyncCall && 'button_on_async'} checkout_form_stripe_button`} type={'submit'} onClick={() => {
                    setErrorMessage('');
                    setSuccessMessage('');
                }}>{isAsyncCall ? 'Please wait...' : "Pay"}</button>
            </div>}
        </form>
    );
}

