import React, { createRef } from "react";
import { connect } from "react-redux";
import { systemAuthBaseUrl } from "../../../config/config";
import { appName } from "../../../config/config";
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import { CardObserver } from "../../components/cards_displayer/cards_displayer";
import NotFound from "../../components/entity_displayer/itemsNotFound";
import ListViewBuilder from "../../components/list_view_builder/listViewBuilder";
import Loader from "../../components/loader/loader";
import notFoundimg from "../../../assets/svgs/noSbPayment.svg";
import './pending_intents.css';
// import moment from 'moment';

class PendingPaymentIntents extends React.Component {

    state = {
        intents: [],
        isAsyncCall: true,
        isAsyncMore: false,
    }

    componentDidMount() {
        this.loading();
    }

    max = 50;

    loading() {
        if (!this.state.isAsyncMore) {
            this.setState({ isAsyncMore: true });
            this.onFetchIntents(this.state.intents.length ? this.state.intents[this.state.intents.length - 1].id : 0).then(async (intents) => {
                await this.setState({ isAsyncMore: false, isAsyncCall: false, });
                if (intents.length) {
                    if (this.state.intents.length < this.max) {
                        const intentsList = [...this.state.intents, ...intents];
                        await this.setState({ intents: intentsList });
                    } else {
                        await this.setState({ intents: intents });
                    }
                } else {
                    await this.setState({ maxReached: true });
                }
            });
        }
    }

    displayerRef = createRef();

    render() {



        return <>

            <ListViewBuilder
                itemCount={this.state.isAsyncMore ? this.state.intents.length + 1 : this.state.intents.length}
                itemBuilder={(index) => {
                    const element = this.state.intents[index];
                    const isTheTrueLastOne = (this.state.intents.length - 1) === index;
                    return Object.keys(element ?? {}).length > 0
                        ? isTheTrueLastOne
                            ? <CardObserver
                                displayerRef={this.displayerRef}
                                onReachBottom={() => {
                                    if (!this.state.maxReached) {
                                        this.loading();
                                    }
                                }}
                                key={index}>{this.intentCard(element)}</CardObserver>
                            : this.intentCard(element)
                        : this.state.isAsyncMore ? <div className="full_space_loader"><Loader /></div> : <div />
                }}
                onReachBottom={() => { }}
            />
            {!this.state.isAsyncCall && <NotFound
                Data={this.state.intents}
                Message={
                    {
                        fr: "Vous n'avez aucune intention de paiement en attente",
                        en: "You have no pending payment intetions",
                        sw: "Huna nia ya malipo inayosubiri"
                    }
                }
                onRetry={(event) => {
                    event.stopPropagation();
                    this.setState({ isAsyncCall: true })
                    this.loading()
                    // console.log("retrying ...")
                }}
                imgToShow={notFoundimg}
            />

            }
        </>
    }

    intentCard(e) {
        return <div key={e.id} className="single_card_history pending_payment_intents_card">
            <div className="card_history_header">
                <h4>{e.CodeRef}</h4>
                <p className="delete_action_p_i" onClick={(event) => {
                    event.stopPropagation();
                    this.onDeleteIntents(e.id);
                }}>Cancel</p>
            </div>
            <div className="card_details">
                <div className="single_detail_content">
                    <p>Date </p>
                    <h4>{new Date(e.createdAt).toLocaleString()}</h4>
                </div>
                <div className="single_detail_content">
                    <p>Subscription plan</p>
                    <h4>{e.SubscriptionType.Name} {e.SubscriptionType.Price} $</h4>
                </div>
            </div>
        </div>
    }

    onFetchIntents(lastId) {
        this.setState({ isAsyncCall: !this.state.isAsyncCall })
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: `api/payment/bank-intent-view-list?offset=${lastId}`,
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: localStorage.getItem(`${appName}act`) ?? ''
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const intents = response.response;
                return intents;
            } else {
                this.setState({ intents: [], isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems([]);
                return [];
            }
        });
    };

    onDeleteIntents(id) {
        this.setState({ isAsyncCall: !this.state.isAsyncCall })
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: `api/payment/bank-intent-delete/${id}`,
            apiMethod: ApiMethods.delete,
            successCodeExpectation: 200,
            requestData: {
                token: { accessToken: localStorage.getItem(`${appName}act`) ?? '' },
                body: {},
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const newIntents = this.state.intents.filter((e) => e.id !== id);
                this.setState({ intents: newIntents, isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems(newIntents);
            } else {
                this.setState({ subscriptionHistory: [], isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems([]);
            }
        });
    };
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    userInformation: storeOf.madoshome.userInformation,
    SubscriptionTypeList: storeOf.madoshome.SubscriptionTypeList,
});

export default connect(mapStateToProps)(PendingPaymentIntents)